<template>
    <div class="swiper_area">
      <div class="swiper course_swiper">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(i, index) in images" :key="index">
            <img
              :src="i.image"
              data-bs-pic="1"
            />
          </div>
        </div>
      </div>
      <div class="myswiper-btn swiper-button-next"></div>
      <div class="myswiper-btn swiper-button-prev"></div>
    </div>
</template>

<script>
import Swiper from "swiper"; // 注意引入的是Swiper
import "swiper/css/swiper.min.css"; // 注意这里的引入

export default {
  name: "Photo",
  data() {
    return {
      images: [
        {image: "img/class-1.jpg" },
        {image: "img/class-2.jpg",},
        {image: "img/class-3.jpg",},
        {image: "img/class-4.jpg",},
        {image: "img/class-5.jpg",},
        {image: "img/class-6.jpg",},
      ],
    };
  },
  mounted() {
    var swiper = new Swiper(".course_swiper", {
      slidesPerView: 1, // 默認顯示 1 個幻燈片
      spaceBetweenSlides: 50, // 默認幻燈片之間的間距為 50px
      centeredSlides: true,
      speed: 1200,
      loop: true,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
      autoplay: {
        delay: 3000,
        disableOnInteraction: false,
      },
      keyboard: true,
      breakpoints: {
        768: {
          slidesPerView: 3, // 在屏幕寬度大於等於 768px 時，每行顯示 3 個幻燈片
          spaceBetweenSlides: 20, // 幻燈片之間的間距設置為 20px
        },
        1200: {
          slidesPerView: 5, // 在屏幕寬度大於等於 1200px 時，每行顯示 5 個幻燈片
          spaceBetweenSlides: 20, // 幻燈片之間的間距設置為 20px
        },
      },
    });
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/sass/course.scss";
</style>