<template>
  <section id="faq">
    <div class="faq">
      <div class="title">
        <h2>常見問題FAQ</h2>
      </div>
      <div class="accordion accordion-flush" id="accordionFlushExample">
        <div
          class="accordion-item"
          v-for="(item, index) in accordionItems"
          :key="index"
        >
          <h2 class="accordion-header" :id="getHeadingId(index)">
            <button
              class="accordion-button collapsed"
              type="button"
              data-bs-toggle="collapse"
              :data-bs-target="getCollapseTarget(index)"
              aria-expanded="false"
              :aria-controls="getCollapseId(index)"
            >
              {{ item.title }}
            </button>
          </h2>
          <div
            :id="getCollapseId(index)"
            class="accordion-collapse collapse"
            :aria-labelledby="getHeadingId(index)"
            data-bs-parent="#accordionFlushExample"
          >
            <div class="accordion-body">{{ item.body }}</div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "Faq",
  data() {
    return {
      accordionItems: [
        {
          title: "關於眉毛 – 做完會像蠟筆小新一樣很粗很黑嗎?",
          body: "不會哦!操作完就像眉筆畫上去一樣自然，脫屑時也只會有薄薄的皮屑脫落",
        },
        {
          title: "關於眉毛 – 做完眉毛可以洗臉碰水嗎?",
          body: "可以的! 可以碰水但清潔用品不能碰到，清洗完拿面紙壓乾保持乾燥即可",
        },
        {
          title: "關於睫毛 – 嫁接睫毛會使真睫毛越來越少嗎?",
          body: "不會!因為本身毛髮都有生長週期，嫁接睫毛是接在距離睫毛根部0.1–0.2MM處，而不是接在眼瞼上，所以假睫毛正常會跟著真睫毛週期脫落",
        },
        {
          title: "關於睫毛 – 為什麼接完睫毛眼皮容易紅腫癢?",
          body: "因為嫁接時距離根部太接近或嫁接服貼度不夠，容易導致假睫毛根部搓到眼皮",
        },
        {
          title: "關於皮膚 – 任何膚質都適合操作藻針嗎?",
          body: "不是哦!蟹足腫、孕婦、糖尿病以及極度敏感膚質不建議使用此課程",
        },
        {
          title: "關於皮膚 – 為什麼擦保養品感覺都無法吸收?",
          body: "因為肌膚屏障受損(肌膚屏障=角質層)，PH值無法平衡，導致保養品擦再多都吸收不了，建議先修復肌膚再針對問題保養",
        },
      ],
    };
  },
  methods: {
    getHeadingId(index) {
      return `flush-heading${index + 1}`;
    },
    getCollapseId(index) {
      return `flush-collapse${index + 1}`;
    },
    getCollapseTarget(index) {
      return `#flush-collapse${index + 1}`;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/sass/home/faq.scss";
</style>