<template>
  <div class="swiper-container">
      <div class="swiper-wrapper">
          <div class="swiper-slide"><img src="image/banner01_mod.jpg" width="100%"></div>
          <div class="swiper-slide"><img src="image/banner02.jpeg" width="100%"></div>
      </div>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination"></div>
      
      <!-- 如果需要导航按钮 -->
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
  </div>
  
</template>

<script>
import Swiper from 'swiper'; // 注意引入的是Swiper
import 'swiper/css/swiper.min.css' // 注意这里的引入

export default {
  name: 'HelloWorld',
  mounted() {
  var mySwiper = new Swiper('.swiper-container', {
    speed: 1200,
            loop: true,
            navigation: {
                nextEl: ".swiper-button-next",
                prevEl: ".swiper-button-prev",
            },
            pagination: {
                el: ".swiper-pagination",
                clickable: true,
            },
            autoplay: {
                delay: 1000,
                disableOnInteraction: false,
            },
            keyboard: true,
  })
}
}
</script>

<style scoped>
.swiper-container {
  width: 500px;
  height: 400px;
}
.swiper-slide {
  text-align: center;
  line-height: 400px;
  color: #b0b0b0;
}
