import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/views/Home.vue';
import Appointment from '@/views/Appointment.vue';
import Course from '@/views/Course.vue';
import Gallery from '@/views/Gallery.vue';
import Health from '@/views/Health.vue';
import Faq from '@/views/Faq.vue';
import Test from '@/views/Test.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { title: '首頁' }
  },
  { 
    path: '/course', 
    name: 'Course', 
    component: Course, 
    meta: { title: '創業教學' } },
  { 
    path: '/gallery', 
    name: 'Gallery', 
    component: Gallery, 
    meta: { title: '作品照片' } 
  },
  { 
    path: '/health', 
    name: 'Health', 
    component: Health, 
    meta: { title: '衛教資訊' } 
  },
  { 
    path: '/appointment', 
    name: 'Appointment', 
    component: Appointment, 
    meta: { title: '預約諮詢' } 
  },
  { 
    path: '/faq', 
    name: 'Faq', 
    component: Faq, 
    meta: { title: '常見問題' } 
  },
  { 
    path: '/test', 
    name: 'Test', 
    component: Test, 
    meta: { title: '測試頁面' } 
  },
];

const router = new VueRouter({
  mode: 'hash',
  routes
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next();
})
export default router;
